@import "../_mixins";

.navbar {
  height: 100px;

  .wrapper {
    max-width: 1366px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;

    @include mobile {
      justify-content: flex-end;
      padding: 20px;
    }

    span {
      font-weight: bold;

      @include mobile {
        display: none;
      }
    }

    .social {
      display: flex;
      gap: 20px;

      img {
        width: 25px;
        height: 25px;

        @include tablet {
          width: 40px;
          height: 40px;
        }

        @include mobile {
          width: 25px;
          height: 25px;
        }
      }
    }
  }
}
