@import "../_mixins";

.services {
  background: linear-gradient(180deg, #0c0c1d, #111132);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .flexContainer {
    display: flex;
  }

  .textContainer {
    flex: 1;
    align-self: flex-end;
    align-items: center;
    gap: 20px;

    @include mobile {
      align-self: center;
      text-align: center;
      flex-direction: column;
    }

    hr {
      width: 500px;
      border: none;
      border-top: 0.5px solid gray;

      @include mobile {
        width: 300px;
      }
    }

    p {
      font-weight: 200;
      font-size: 20px;
      color: gray;
      text-align: right;

      @include mobile {
        font-size: 15px;
      }
    }
  }
  .titleContainer {
    flex: 2;
    display: flex;
    align-items: center;
    flex-direction: column;

    @include mobile {
      width: 100%;
    }

    .title {
      align-items: center;
      gap: 50px;

      @include mobile {
        flex-direction: column;
        text-align: center;
        gap: 20px;
      }

      img {
        width: 300px;
        height: 100px;
        border-radius: 50px;
        object-fit: cover;

        @include mobile {
          width: 200px;
          height: 50px;
        }
      }

      h1 {
        font-size: 96px;
        font-weight: 100;

        @include mobile {
          font-size: 36px;
        }
      }

      button {
        width: 300px;
        height: 100px;
        border-radius: 50px;
        background-color: orange;
        border: none;
        font-size: 24px;
        cursor: pointer;
        text-transform: uppercase;

        @include mobile {
          font-size: 16px;
          width: 150px;
          height: 50px;
        }
      }
    }
  }
  .listContainer {
    flex: 2;
    max-width: 1366px;
    margin: auto;

    @include mobile {
      flex-direction: column;
      width: 100%;
    }

    .box {
      padding: 50px;
      border: 0.5px solid gray;
      flex-direction: column;
      justify-content: space-between;
      text-align: center;

      @include mobile {
        border: none;
        align-items: center;
        gap: 15px;
        padding: 10px;
        font-size: 13px;
      }
    }

    p {
      @include mobile {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
      }
    }

    button {
      padding: 10px;
      background-color: orange;
      border: none;
      cursor: pointer;
      border-radius: 50px;

      @include mobile {
        background-color: transparent;
        border: 1px solid orange;
        border-radius: 5px;
        color: orange;
        width: 50%;
        padding: 5px;
      }
    }
  }
}

.textContainer,
.titleContainer,
.title,
.box,
.listContainer {
  @extend .flexContainer;
}
